import { NextResponse } from 'next/server'
import { prisma } from '@/lib/prisma'
import dotenv from 'dotenv'
dotenv.config({ path: process.cwd() + '/app/api/crystalpay/.env.local' });

export async function POST(request: Request) {
  try {
    const bodyJson = await request.json();
    let { amount, description, userId, currency = "USD" } = bodyJson;

    // Импорт из process.env через dotenv
    const auth_login = process.env.authLogin;
    const auth_secret = process.env.authSecret;
    let redirect_url = process.env.REDIRECT_URL || "localhost:3000/dashboard/billing";

    if (!auth_login || !auth_secret) {
      console.error('Credentials missing');
      return NextResponse.json({ error: 'Отсутствуют credentials CrystalPay' }, { status: 500 });
    }

    if (!redirect_url.startsWith("http")) {
      redirect_url = `https://${redirect_url.replace(/^\/+/, "")}`;
    }

    amount = Number.parseFloat(amount);
    if (isNaN(amount) || amount < 10) {
      return NextResponse.json({ error: 'Минимальная сумма пополнения: $10.00 USD' }, { status: 400 });
    }
    amount = Number(amount.toFixed(2));

    if (!userId || typeof userId !== "string") {
      return NextResponse.json({ error: 'Отсутствует userId, попробуйте обновить страницу' }, { status: 400 });
    }

    const payload = {
      auth_login,
      auth_secret,
      amount,
      lifetime: 60,
      currency,
      type: "purchase",  // Добавлено для фикса ошибки "Type field is required" (альтернатива: "topup")
      description: description || "Пополнение баланса через CrystalPay",
      extra: `userId:${userId}`,
      redirect_url,
    };

    console.log('Payload to CrystalPay:', payload); // Лог для отладки

    const crystalpayRes = await fetch("https://api.crystalpay.io/v3/invoice/create/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(payload),
    });

    const cpBody = await crystalpayRes.json();
    console.log('CrystalPay response:', cpBody); // Лог ответа (проверьте терминал на ошибки API)

    if (!crystalpayRes.ok || cpBody.error) {
      const message = cpBody.errors ? cpBody.errors.join(", ") : (cpBody.error || "Ошибка CrystalPay, попробуйте позже.");
      console.error('CrystalPay error:', message);
      return NextResponse.json({ error: message }, { status: 400 });
    }

    // Сохраняем транзакцию в БД как pending
    await prisma.transaction.create({
      data: {
        userId,
        amount: Number(cpBody.amount ?? amount),
        currency: cpBody.currency ?? currency,
        method: 'crystalpay',
        status: 'pending',
        externalId: String(cpBody.id),
      },
    })

    return NextResponse.json({
      url: cpBody.url,
      id: cpBody.id,
      amount: cpBody.amount,
      currency: cpBody.currency,
      description: cpBody.description,
    });
  } catch (err: any) {
    console.error('Server error:', err.message);
    return NextResponse.json({ error: err.message || "Internal server error" }, { status: 500 });
  }
}